/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) { 
    /* BLOC 1 */
    .articlesRow {
        margin-top: 0px;
        margin-bottom: 0px;
    }
    .ceMoisArticleContainer {
        margin-bottom: 20px;
    }

    .imageCeMois {
        /* max-height: 100px;
        min-height: 100px; */
        margin-bottom: 10px;
    }

    .titleCeMois {
        font-family: 'Poppins Bold';
        font-size: 15px;
    }

    .dateCeMois {
        font-family: 'Poppins SemiBold';
        font-size: 12px;
        color: '#666666';
    }

    .bigImageBloc1 {
        /* max-height: 623px;
        min-height: 623px; */
        width: 100%;
    }

    .news-big-img {
        position: relative;
        margin-bottom: 20px;
    }
      
    .news-big-img .content {
        position: absolute; /* Position the background text */
        bottom: 0; /* At the bottom. Use top:0 to append it to the top */
        background: rgb(0, 0, 0); /* Fallback color */
        background: rgba(0, 0, 0, 0.5); /* Black background with 0.5 opacity */
        color: white; /* Grey text */
        width: 100%; /* Full width */
        padding: 7px; /* Some padding */
    }

    .news-big-img .content h3 {
        padding : 5px;
        font-size: 12px;
    }

    .news-big-img .content p {
        font-size: 12px;
    }
    /* BLOC 1 */
    
    /* SOCIAL LINKS */
    .socialLinka h4 {
       font-size: 20px;
       font-family: 'Poppins Bold';
    }




    /* SOCIAL LINKS */


    .sectionTitleRowActualites {
        margin-left: 1px;
        margin-right: 1px;
    }
    .sectionTitleContainerActualites {
        padding-top: 15px;
        padding-bottom: 15px; 
        padding-left: 30px;
        padding-right: 30px;
        background-color:black;
        display: flex;
        justify-content: center;    
    }

    .sectionTitleActualites {
        color: white;
        font-family: 'Poppins Bold';
        font-size: 20px;
        text-align: 'center';
    }

    .titleSeperator {
        border-color: black; 
        margin-top: -2px;
        border-width: 3px;
        margin-bottom: 30px; 
        width: 100%;
    }
}

